:root {
  --main-font: "Roboto", sans-serif;
  --main-txt-cl: #000;
  --accent-txt-cl: #ff6b08;
  --light-txt-color: #fff;
  --error-txt-color: #ff001b;
  --current-link: #ff001b;
  --footer-txt-color: #545454;
  --accent-bg-cl: #fff;
  --footer-bg-color: #f7f7f7;
  --blu-bg-color: #005bbb;
  --yelow-bg-color: #ffd500;
  --main-grey: #10181d;
  --btn-bg-color: #ff6b01;
  --btn-dis-bg-color: transparent;
  --bg-color: #22272b;
  --cards-txt-color: #8f95a3;
  --dark-bg-footer: #10181d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--main-font);
  color: var(--light-txt-color);
  background-color: var(--accent-bg-cl);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
}

::-webkit-scrollbar-track {
  background-color: var(--main-grey);
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(transparent, var(--current-link) );
  border-radius: 6px;
}

body.light-theme {
  background-color: var(--accent-bg-color);
}

body.dark-theme {
  background-color: var(--dark-bg-color);
}

body.dark-theme .film-card__title {
  color: var(--cards-txt-color);
}

.light-theme main {
  background-color: #fff;
  transition: background-color .5s;
}

.light-theme footer {
  transition: background-color .5s, color .5s;
}

.dark-theme main {
  background-color: var(--bg-color);
  transition: background-color .5s;
}

.dark-theme footer {
  background-color: var(--dark-bg-footer);
  transition: background-color .5s, color .5s;
}

.dark-theme footer .footer__item {
  color: var(--light-txt-color);
}

.dark-theme footer .footer__btn {
  color: var(--cards-txt-color);
  transition: color .2s;
}

.dark-theme footer .footer__btn:hover {
  color: var(--btn-bg-color);
}

.dark-theme .modal-card {
  background-color: var(--dark-bg-footer);
}

.dark-theme .modal-card__title, .dark-theme .modal-card__about-title {
  color: var(--light-txt-color);
}

.dark-theme .modal-card__about-descr {
  color: #8c8c8c;
}

.dark-theme .modal-card .modal-card__info-title-list p {
  color: var(--light-txt-color);
}

.dark-theme .films-gallery {
  background-color: var(--bg-color);
  position: relative;
}

.dark-theme #pagination {
  background-color: var(--bg-color);
}

.dark-theme #pagination .tui-page-btn {
  color: #fff;
  transition: background-color .5s;
}

.dark-theme #pagination .tui-page-btn.tui-last-child .tui-ico-ellip {
  color: #fff;
}

.dark-theme #pagination .tui-page-btn.tui-first, .dark-theme #pagination .tui-page-btn.tui-prev, .dark-theme #pagination .tui-page-btn.tui-next, .dark-theme #pagination .tui-page-btn.tui-last {
  background-color: var(--dark-bg-footer);
}

.dark-theme #pagination .tui-page-btn.tui-is-disabled {
  color: #8c8c8c;
  background-color: #333;
}

.dark-theme #q-pagination {
  background-color: var(--bg-color);
}

.dark-theme #q-pagination .tui-page-btn {
  color: #fff;
  transition: background-color .5s;
}

.dark-theme #q-pagination .tui-page-btn.tui-last-child .tui-ico-ellip {
  color: #fff;
}

.dark-theme #q-pagination .tui-page-btn.tui-first, .dark-theme #q-pagination .tui-page-btn.tui-prev, .dark-theme #q-pagination .tui-page-btn.tui-next, .dark-theme #q-pagination .tui-page-btn.tui-last {
  background-color: var(--dark-bg-footer);
}

.dark-theme #q-pagination .tui-page-btn.tui-is-disabled {
  color: #8c8c8c;
  background-color: #333;
}

ul {
  list-style-type: none;
}

a {
