.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  width: 129.53px;
  height: 44px;
  background: var(--btn-bg-color);
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn:disabled {
  background-color: var(--btn-dis-bg-color);
  border: 1px solid #fff;
}

.info-card {
  width: 85%;
  text-align: center;
  max-width: 400px;
  height: 150px;
  color: #000;
  border: 1px solid #b8b4b4;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader {
  width: 100%;
  height: 100%;
  background-color: var(--main-grey);
  z-index: 100;
  transition: opacity 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader__container path {
  stroke: var(--btn-bg-color);
  stroke-dasharray: 8600;
  animation: draw 2.5s cubic-bezier(0, 0, 1, .56) forwards;
}

.preloader--hide {
  opacity: 0;
}

@keyframes draw {
  0% {
    stroke-dashoffset: 8600px;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

.loading {
  width: 100px;
  height: 180px;
  z-index: 99;
  transition: opacity .3s;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
}

.loading--hide {
  opacity: 0;
}

.loading__spinner {
  height: 100px;
  width: 100px;
  border-top: 7px solid rgba(0, 0, 0, 0);
  border-left: 7px solid var(--current-link);
  border-bottom: 4px solid var(--current-link);
  border-radius: 50%;
  animation: rotate 1s cubic-bezier(0, 0, 1, .7) infinite;
  position: relative;
}

.loading__spinner:after {
  content: "";
  height: 6px;
  width: 6px;
  background-color: var(--current-link);
  border-radius: 50%;
  position: absolute;
  top: 7px;
  left: 7px;
}

.loading__logo {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 28%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading__text {
  color: var(--main-grey);
  text-align: center;
  margin-top: 15px;
  font-family: tahoma;
  font-weight: bold;
  animation: colorchange 5s linear infinite;
}

@keyframes colorchange {
  0% {
    color: var(--main-grey);
  }

  50% {
    color: var(--main-txt-cl);
  }

  100% {
    color: var(--main-grey);
  }
}

@keyframes rotate {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(360deg);
  }
}

