.trailer-btn {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background: radial-gradient(#ff6b08 60%, #fff 62%);
  border: 1px solid #ff6b08;
  border-radius: 50%;
  justify-self: center;
  position: absolute;
  box-shadow: 0 0 25px 3px #ff6b08;
}

.trailer-btn:after {
  content: "";
  transform-origin: center;
  width: 0;
  height: 0;
  z-index: 100;
  border-top: 7px solid rgba(0, 0, 0, 0);
  border-bottom: 7px solid rgba(0, 0, 0, 0);
  border-left: 13px solid #fff;
  transition: all .4s cubic-bezier(.55, .055, .675, .19);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.trailer-btn:before {
  content: "";
  width: 150%;
  height: 150%;
  opacity: 1;
  border: 5px solid #ff6b08;
  border-radius: 100%;
  animation: pulsate1 2s infinite;
  position: absolute;
  top: -40%;
  left: -40%;
}

@keyframes pulsate1 {
  0% {
    opacity: 1;
    transform: scale(.6);
    box-shadow: inset 0 0 40px 3px #ff6b08, 0 0 25px 10px rgba(255, 255, 255, .918);
  }

  100% {
    opacity: 0;
    box-shadow: none;
    transform: scale(1);
  }
}

.btn-up {
